.swiper-pagination-lock {
  display: none;
}

.gallery-carousel {
  display: block;
}
.gallery-carousel img {
  max-width: 100%;
}
.gallery-carousel__arrow {
  right: unset !important;
  left: unset !important;
  width: unset !important;
  height: unset !important;
  color: #8596a2 !important;
}
.gallery-carousel__arrow::after {
  font-weight: 700 !important;
  font-size: 1.25rem !important;
}
.gallery-carousel__slide .with-shadow {
  box-shadow: 4px 4px 0 0 rgba(0, 27, 64, 0.1);
}
.gallery-carousel__pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  margin: 0 6px;
}